.reports-analysis-container {
    padding: 20px;
    font-family: Arial, sans-serif;
}

.reports-buttons {
    display: flex;
    gap: 20px;
    margin-top: 20px;
}

.reports-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.reports-button:hover {
    background-color: #0056b3;
}

/* Responsive tweaks for mobile and tablet */
@media (max-width: 900px) {
  .container, .main, .page-container, [class*="container"] {
    max-width: 100vw !important;
    padding: 10px !important;
    box-sizing: border-box;
  }
  .sidebar {
    position: fixed;
    left: -260px;
    top: 0;
    width: 250px;
    height: 100vh;
    z-index: 1000;
    transition: left 0.3s;
  }
  .sidebar.open {
    left: 0;
  }
  .sidebar-toggle {
    display: block;
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 1100;
    background: #228b22;
    color: #fff;
    border: none;
    font-size: 2rem;
    border-radius: 5px;
    padding: 5px 10px;
  }
  .sidebar-backdrop {
    display: block;
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0,0,0,0.3);
    z-index: 999;
  }
}
@media (max-width: 600px) {
  h1, .header-title {
    font-size: 1.5rem !important;
  }
  .container, .main, .page-container, [class*="container"] {
    padding: 5px !important;
    font-size: 1rem !important;
  }
}